import { useTranslation } from "react-i18next";

import { HvButton, useTheme } from "@hitachivantara/uikit-react-core";

import IconUiKit from "../../../../../IconUiKit";
import AppShellEvents from "../../../../../../lib/types/events";

const ColorModeSwitcher = () => {
  const { t } = useTranslation();
  const { colorModes } = useTheme();

  const changeColorModeHandler = () => {
    const customEvent = new CustomEvent(AppShellEvents.ThemeEvent, {
      detail: {
        colorMode: undefined
      }
    });
    globalThis.dispatchEvent(customEvent);
  };

  return (
    colorModes.length > 1 && (
      <HvButton
        icon
        onClick={changeColorModeHandler}
        variant="secondaryGhost"
        aria-label={t("header.colorModeSwitcher.ariaLabel") ?? ""}>
        <IconUiKit name="ThemeSwitcher" />
      </HvButton>
    )
  );
};

export default ColorModeSwitcher;
